
.app__chef-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 3rem;
}

.app__chef-content_quote {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;

}

.app__chef-content_quote img {
    width: 48px;
    height: 40px;
    margin: 0 1rem 3rem 0;
}

.app__chef-sign img {
    width: 50%;
    margin: 2rem;
}

@media screen and (max-width: 768px) {
    .app__wrapper_info {
        margin-top: 2rem;
    }
    #chef-word {
        font-size: 2.5rem;
    }
    .app__chef-content_quote p{
        font-size: 1rem;
    }
    .app__chef-content_quote img {
        width: 28px;
        height: 20px;
        margin: 0 1rem 7rem 0;
    }

}