

 .app__specialMenu {
    display: flex; 
    flex-direction: column;
    background-color: #28282B;
}

.app__specialMenu_title {
    margin-bottom: 2rem;
    text-align: center;
}

.app__specialMenu_menu {
    width: 100%;
    margin: 2rem 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row; /* Default direction */
}

.app__specialMenu_menu_heading {
    font-family: var(--font-base);
    font-weight: 600;
    font-size: 45px;
    line-height: 58px;
    color: var(--color-golden);
    letter-spacing: 0.04em;
}

.app__specialMenu_menu_wine, 
.app__specialMenu_menu_cocktails {
    flex: 1;
    width: 100%;
    flex-direction: column; 
}

.app__specialMenu_menu_img {
    width: 410px;
    margin: 0 2rem;
}
.app__specialMenu_menu_img img {
    width: 100%;
    height: auto;
}

.app__specialMenu_menu_items {
    display: flex;
    flex-direction: column;
    margin: 2rem 0;
    width: 100%;
}

@media screen and (max-width: 1150px) {
    .app__specialMenu_menu {
        flex-direction: column; 
        align-items: center;
    }
   
}

@media screen and (max-width: 650px) {
    .app__specialMenu_menu_img {
        width: 100%;
    }
  
}
