
.app__newsletter {
    padding: 2rem 4rem;
    border: 1px solid var(--color-golden);
    z-index: 100;
    background: var(--color-black);
}
.app__newsletter-heading {
    text-align: center;
}
.app__newsletter-input {
    flex-direction: row;
    margin-top: 3rem;
}

.app__newsletter-input input {
    width: 620px;
    border: 1px solid var(--color-golden);
    border-radius: 5px;
    font-size: 1.2rem;
    font-family: var(--font-base);
    padding: 0.6rem;
    margin-right: 1rem;
    background: var(--color-black);
    color: white;
}

@media screen and (max-width:990px) {
    .app__newsletter-input {
        flex-direction: column;
        width: 100%;
    }
    .app__newsletter-input input {
        margin: 0 0 2rem 0;
        width: 100%;
    }
}

@media screen and (max-width:650px){
    .app__newsletter {
        padding: 2rem 1rem;
        
    }
    .app__newsletter-heading h1{
        font-size: 32px;
        line-height: 50px;
    }
}
