.app__menuItem {
    width: 100%;
    margin: 1rem;
    display: flex;
    flex-direction: column;
}

.app__menuItem-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.app__menuItem-sub {
    width: 100%;
    margin-top: 0.2rem;
}

.app__menuItem-name {
    flex: 1;
}
.app__menuItem-dash {
    width: 70px;
    height: 1px;
    background: antiquewhite;
   
    margin: 0 12px;
}

.app__menuItem-price {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}