
.app__footerOverlay {
    width: 100%;
    height: 100%;
    z-index: 1;
    display: flex;
    flex-direction: column;
    position: absolute;
}

.app__footerOverlay-black {
    height: 25%;
    background: var(--color-black);
}
.app__footerOverlay-img {
    height: 75%;
} 

@media screen and (max-width:450px) {
    .app__footerOverlay {
        width: 100%;
    }
    .app__footerOverlay-black {
        width: 100%;

    }
   
}